import React from 'react';
import PlantillaProyectos from '../plantillaproy';
import {Grid } from 'react-mdl';
import Covid19Img from '../../img/sistemacovid19.png'
import WebDevImg from '../../img/web-dev.png'



class ProyectosReact extends React.Component {
     
  render() { 
    return (           
      <Grid className="proyecto-grid">       
        <PlantillaProyectos 
          bgImagen={WebDevImg}
          nombreproyecto="Mi Portafolio"
          descripcion="Mi primera aplicacion realizada en React Js 100%, consta de una landing page
          como componente principal, usando React-MDL como Layout"
          url="/portafoliopro"
          categoria="React JS"
          />
          <PlantillaProyectos 
          bgImagen={Covid19Img}
          nombreproyecto="Sistema Covid 19"
          descripcion="Aplicación para el seguimiento del Covid 19, reportes diarios global y por pais, 100% en React Js"
          url="http://livecovid19.tk"
          categoria="React JS"
          />
      </Grid>                           
             
    );
  }
}
 
export default ProyectosReact;