import React from 'react';
import { Grid } from 'react-mdl';
import PlantillaProyectos from '../plantillaproy';
import DerilacImg from '../../img/derilac_650x350.png'
import QuanthykaImg from '../../img/quanthyka_650x350.png'
import FirenetImg from '../../img/firenet_650x350.png'
import LeonPaulImg from '../../img/leonpaul_650x350.png'

class Wordpress extends React.Component {
     
  render() { 
    return ( 
          
      <Grid className="proyecto-grid">
      <PlantillaProyectos 
       bgImagen={DerilacImg}
       nombreproyecto="Lacteos Derilac"
       descripcion="Hecha en Wordpress para la empresa Lacteos Derilac ubicada en Venezuela, 
       solo requeria una pagina sencilla con un botón para descargar lista de precios."
       url="https://lacteosderilac.com"
       categoria="Wordpress"
      />
      <PlantillaProyectos 
        bgImagen={QuanthykaImg}
        nombreproyecto="Quanthyka"
        descripcion="Hecha en Wordpress para la empresa Quanthyka ubicada en Colombia, 
        se le rediseño la página a una mas corporativa y se le configuraron los correos."
        url="http://quanthyka.com"
        categoria="Wordpress"
       />
       <PlantillaProyectos 
       bgImagen={FirenetImg}
       nombreproyecto="Firenet Colombia"
       descripcion="Hecha en Wordpress para la empresa Firenet Colombia ubicada en Bucaramanga,Colombia, 
       se le re diseño la pagina a una más corporativa y más llamativa, se le da soporte técnico mensual."
       url="https://firenetcolombia.com.co"
       categoria="Wordpress"
      />       
       <PlantillaProyectos 
       bgImagen={LeonPaulImg}
       nombreproyecto="Leon Paul Sudamerica"
       descripcion="Hecha en Wordpress para la empresa Leon Paul Sudámerica ubicada en Lima - Perú, 
       se le integro woocomerce para venta de productos para esgrima."
       url="https://leonpaul-sudamerica.com"
       categoria="Wordpress"
      />
       
   </Grid>                        
             
    );
  }
}
 
export default Wordpress;