import React from 'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl';
import Footer from './footer';
import Imagen from '../img/bg_yo2.jpg';
 
import Habilidades from './habilidades';

class Contacto extends React.Component {
     
  render() { 
    return ( 
      <div className="contacto">
        <Grid className="grid contacto">
          <Cell col={6}>
            <h2>Luis Castillo</h2>
            <hr/>
            <h4>Desarrollador Web Frontend</h4>            
            <img className="foto-contacto" src={Imagen} alt="Luis Castillo" />            
             
            <h3>Habilidades</h3>
                        
            <Habilidades 
              habilidades="HTML/CSS"
              progress={95}
            />  
            <Habilidades 
              habilidades="JS"
              progress={75}
            />              
            <Habilidades 
              habilidades="ReactJS"
              progress={65}
            />  
            <Habilidades 
              habilidades="PHP"
              progress={65}
            />                      
          </Cell>
          <Cell col={6}>
            <h2>Contáctame</h2>
            <hr/>
            <div className="contenido-lista">
              <List>
                <ListItem>
                  <ListItemContent className="item-contacto">                
                    <i className="fa fa-phone-square" aria-hidden="true"></i>
                      +51 917334580                  
                  </ListItemContent>
                </ListItem>
                <ListItem>
                <ListItemContent className="item-contacto">
                  <a href="mailto:castilloluis2005@gmail.com">
                      <i className="fa fa-envelope" aria-hidden="true"></i>
                        castilloluis2005@gmail.com
                  </a>
                </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent className="item-contacto">
                    <a href="https://instagram.com/luis.castilloweb">
                    <i className="fa fa-instagram" aria-hidden="true"></i>
                      @luis.castilloweb
                    </a>
                  </ListItemContent>
                </ListItem>
              </List>
            </div>           
          </Cell>
          <Footer />
        </Grid>
      </div>
    );
  }
}
 
export default Contacto;