import React  from 'react';
import {Grid, Cell, List, ListItem, ListItemContent, Button} from 'react-mdl'; 
import Footer from './footer';
import Imagen from '../img/bg_yo.jpg';
import ImagenSoporte from '../img/flat-iconos/apoyo-tecnico.png';
import ImagenWeb from '../img/flat-iconos/codigo.png';
import ImagenEcommerce from '../img/flat-iconos/pantalla.png';
import ImagenSeo from '../img/flat-iconos/estadisticas.png';

class Acercade extends React.Component{
    render(){
      return(
        <div className="acercade">
          <Grid className="grid acercade">
            <Cell col={6}>
              <div className="imagen-acercade">
                <img                    
                  src={Imagen} 
                  alt="Luis Castillo" 
                />
              </div>
              <h2 className="acercade-titulo">Luis Castillo</h2>
              <h4 className="acercade-subtitulo">Programador Web Frontend</h4>
              <hr/>
              <p>Soy un desarrollador  y diseñador web completo con sede en Lima, Perú, pero trabajo en todo el mundo. 
                Tengo todas las habilidades para crear un nuevo sitio web de alta calidad o mejorar 
                sitios web existentes para individuos y organizaciones, utilizando las últimas técnicas 
                y tendencias. Me concentro en soluciones de sitios web modernas y receptivas que son fáciles 
                de actualizar por el cliente. ¿Está buscando un sitio web de alta calidad para su organización 
                o empresa a un precio competitivo? </p>
                <Button raised accent><a href="/contacto">Contáctame</a></Button>
            </Cell>
            <Cell className="acercade-derecho" col={6}>
              <h2>Servicios</h2>
              <hr/>
            <div className="lista servicios">
                <List>
                  <ListItem>
                    <ListItemContent>                
                      <i ><img src={ImagenWeb} alt="desarrollo web"/></i>
                        <span className="titulo-servicios">Desarrollo Web Frontend</span>
                        <p>Desarrollo sitios web personalizados, manteniéndolos únicos, funcionales y modernos. Teniendo en cuenta las últimas tecnologías como HTML5, CSS3, JS, React y más.</p>                 
                    </ListItemContent>
                  </ListItem>
                  <ListItem>
                    <ListItemContent >                 
                          <i><img src={ImagenSoporte} alt="soporte tecnico"/></i>
                        <span className="titulo-servicios">Soporte y Mantenimiento de Sitios</span>
                        <p>Desde la construcción de su primer sitio web hasta el mantenimiento de sus aplicaciones existentes. Proporciono soporte continuo y servicios de mantenimiento para garantizar que su sitio web siempre funcione sin problemas.</p>               
                    </ListItemContent>
                  </ListItem>
                  <ListItem>
                    <ListItemContent >                  
                      <i ><img src={ImagenSeo} alt="seo web"/></i>
                      <span className="titulo-servicios">Optimización SEO</span>
                        <p>Realizo un proceso de estudio de su página web en torno al diseño, el contenido y la estructura del mismo. Analizamos cuales son las palabras clave más relevantes para su empresa y a continuación llevo a cabo todas las mejoras que sean necesarias para que su web mejore su posición en los buscadores.</p>                  
                    </ListItemContent>
                  </ListItem>
                  <ListItem  >
                    <ListItemContent>                  
                      <i ><img src={ImagenEcommerce} alt="ecomerce"/></i>
                      <span className="titulo-servicios">E-Commerce</span>
                      <p>Si necesita una tienda en línea, permítame ayudarlo a crear una solución profesional de acuerdo con sus requisitos y poner en marcha su negocio en línea. He trabajado con Prestashop, WooCommerce y varios otros sistemas de comercio electrónico.</p>                 
                    </ListItemContent>
                  </ListItem>
                </List>
            </div>         
            </Cell>
            <Footer /> 
          </Grid>
        </div>
         
      );
  }
}

export default Acercade; 