import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Cell, Card, CardTitle, CardText, Button, CardActions, CardMenu} from 'react-mdl';

class PlantillaProyectos extends Component {
  
    render() {
      const { bgImagen, nombreproyecto, descripcion, url, categoria, } = this.props
        return (          
            <Cell col={6}>
                <Card shadow={0} className="card">                
                  <CardTitle className="card-titulo" style={{background:`url(${bgImagen}) center`, backgroundColor:`#2193b0`, backgroundSize:'cover'}}>                   
                    {nombreproyecto}                                    
                  </CardTitle>
                  <CardText>
                    {descripcion}
                  </CardText>
                  <CardActions border>
                    <Button className="btn accion"><a href={url} target="_blank">Ver en Acción</a></Button>
                    <Button className="btn accion der"><Link to="/contacto">Contáctame</Link></Button>
                  </CardActions>
                  <CardMenu>
                      <Button className="btn cat">{categoria}</Button>
                  </CardMenu>
                </Card>
            </Cell>
        );
    }
}
 
export default PlantillaProyectos;