import React from 'react';
import { Grid } from 'react-mdl'; 
import Wordpress from '../categorias/wordpress'
import ProyectosReact from '../categorias/react'

class Todos extends React.Component {
     
  render() { 
    return (           
      <Grid className="proyecto-grid">
        <Wordpress />
        <ProyectosReact />
      </Grid>             
    );
  }
}
 
export default Todos;