import React from 'react';

import {Grid, Cell, Button} from 'react-mdl';
import Imagen from '../img/yo.png';
import { Link } from 'react-router-dom';

class LandingPage  extends React.Component {
     
    render() { 
        return ( 
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className="landing-grid">
                    <Cell col={12}>
                        <img 
                            src={Imagen}
                            alt= "avatar-luis"
                            className="avatar-img" 
                        />
                        <div className="banner-text">
                            <h1>Frontend Web Developer</h1>
                            
                            <p>HMTL | CSS | JS | React | Wordpress | Joomla | Soporte Técnico</p>
                            {/*LinkedIn*/}
                            <div className="social-links">
                                <a href="https://linkedin.com/in/luiscastilloweb" target="_blank" rel="noopener noreferrer" aria-hidden="true"><i className="fa fa-linkedin-square"></i></a>
                             
                            {/*Git*/}
                             
                                <a href="https://github.com/luiscastillo-webdev" target="_blank" rel="noopener noreferrer" aria-hidden="true"><i class="fa fa-github" aria-hidden="true"></i></a>
                             
                            {/*Instagram*/}
                             
                                <a href="https://instagram.com/luis.castilloweb" target="_blank" rel="noopener noreferrer" aria-hidden="true"><i class="fa fa-instagram" aria-hidden="true"></i></a>
                            </div>
                            <hr />
                            <div className="cta">
                                <Button className="btn-accion izq" raised accent><Link to="/proyectos">Ver Mis Proyectos</Link></Button>
                                <Button className="btn-accion der" raised><Link to="/contacto">Contacto</Link></Button>
                            </div>
                        </div>
                    </Cell>
                </Grid>
            </div>
         );
    }
}
 
export default LandingPage;