import React from "react";
import { Cell, Grid, Button} from 'react-mdl';
import { Link } from 'react-router-dom';
import ErrorImagen from '../img/error_404.jpg';


const PageNotFound = () => (
//Fragment
  <Grid className="nofound-page">
    <Cell col={12}>
      <div className="contenido">
       <img className="error-img" src={ErrorImagen} />
       <Button className="btn error"><Link exact path to="/">Volver</Link></Button>
       </div>
    </Cell>
  </Grid>
)
export default PageNotFound;